<template>
  <div class="column is-narrow-desktop is-4-mobile is-narrow-tablet">
    <img :src="(imageurl) | image" class="poster" />
  </div>
</template>

<script>
export default {
  props: ["imageurl"],
  filters: {
    image(url) {
      return url != null
        ? "https://image.tmdb.org/t/p/w500/" + url
        : "../assets/unknown.png";
    }
  }
};
</script>

<style scoped>
.poster {
  max-width: 10.5rem;
  max-height: 10.5rem;
  border-radius: 5px;
}
</style>